import React from 'react'
import { useRef, useEffect, useState } from "react";
import images from '../../constants/images';
import ReviewForm from './UserReview/userReview';
import TypeReview from './Type Review/TypeReview';
import ReviewPhone from '../Phones/ReviewPhone/ReviewPhone';
import ScrollContainer from 'react-indiana-drag-scroll'

import './Review.css';



const Review = () => {
  
  const [ isOpen, setIsOpen] = useState(false);


  return(
    <>
      <div className='app__review' id='reviews'>
        <div className='app__review-content'>
          <div className='topbar'>
            <button className='topbar-createreview' onClick={() => setIsOpen(true)}> </button>
            <p className='topbar-reviewsign'></p>
            <img src={images.starrate} alt="Star rate" />
            <p className='topbar-rate'>4</p>
          </div>
          <ScrollContainer className='Reviews'> 
            <ReviewForm 
              Name=""
              PositionSign="Co-Ceo" 
              NameOfCompany="Feed Motion ADS" 
              Stars="5"
              Review="   .       .       .       .       .    egegegegerggerger"
            />
            <ReviewForm 
              Name=""
              PositionSign="Co-Ceo" 
              NameOfCompany="Feed Motion ADS" 
              Stars="2"
              Review="   .    "
            />
            <ReviewForm 
              Name=""
              PositionSign="Co-Ceo" 
              NameOfCompany="Feed Motion ADS" 
              Stars="2"
              Review="   .    "
            />
            <ReviewForm 
              Name=""
              PositionSign="Co-Ceo" 
              NameOfCompany="Feed Motion ADS" 
              Stars="2"
              Review="   .    "
            />
            <ReviewForm 
              Name=""
              PositionSign="Co-Ceo" 
              NameOfCompany="Feed Motion ADS" 
              Stars="2"
              Review="   .    "
            />
            <ReviewForm 
              Name=""
              PositionSign="Co-Ceo" 
              NameOfCompany="Feed Motion ADS" 
              Stars="2"
              Review="   .    "
            />
          </ScrollContainer>
        </div>
        <div className='Arrowsix'>
          <img src={images.arrowsix} alt="Arrow six" />
        </div>
        
        {isOpen && (
          <div className='app__review_typereview-blurSubstrate' onClick={() => setIsOpen(false)}>
            <TypeReview />
          </div>  
        )
        }
      </div>
    </>
  )
}

export default Review