import React, { useState } from 'react'
import ReviewForm from '../../Review/UserReview/userReview';
import images from '../../../constants/images';
import ScrollContainer from 'react-indiana-drag-scroll'


import './ReviewPhone.css';
import TypeReview from '../../Review/Type Review/TypeReview';

const ReviewPhone = () => {

  const [ phoneTypeReview, setPhoneTypeReview ] = useState(false)

  return (
    <div className='app__reviewphone' id='reviewsphone'>
      <div className='review-label'>
        <button className='review-label-button' onClick={() => setPhoneTypeReview(true)}> </button>
        <p className='review-label-review'></p>
        <img className='review-label-star' src={images.starrate} alt="star"/>
        <p className='review-label-star-rate'>4</p>
      </div>
      <ScrollContainer className='Reviews'>
        <ReviewForm 
          Name=""
          PositionSign="Co-Ceo" 
          NameOfCompany="Feed Motion ADS" 
          Stars="5"
          Review="   .    "
        />
        <ReviewForm 
          Name=""
          PositionSign="Co-Ceo" 
          NameOfCompany="Feed Motion ADS" 
          Stars="5"
          Review="   .       .     "
        />
        <ReviewForm 
          Name=""
          PositionSign="Co-Ceo" 
          NameOfCompany="Feed Motion ADS" 
          Stars="5"
          Review="   .       .  "
        />
        <ReviewForm 
          Name=""
          PositionSign="Co-Ceo" 
          NameOfCompany="Feed Motion ADS" 
          Stars="5"
          Review="   .       .       .       .       .    egegegegerggerger"
        />
      </ScrollContainer>

      <img className='review_palm' src={images.cutfullpalm} alt="Full palm"/>
      <img className='review_palm2' src={images.cutfullpalm} alt="Full palm"/>
      <img className='review_palm3' src={images.cutpalm} alt="Full palm"/>
      <img className='arrowphone_contactus' src={images.phonearrowfife} alt="arrow"/>
      {phoneTypeReview && 
        <div className='app__phoneprice-ordermeeting-backsubstrate' onClick={() => setPhoneTypeReview(false)}>
          <img className='order-content-phoneimage' src={images.tree} alt='Black tree with white background' />
          <TypeReview />
        </div>
      }
    </div>
  )
}

export default ReviewPhone